.App {
  text-align: center;
  /* background-color: rosybrown; */
  
  animation-name: fadeIn;
  animation-duration: 1s;
}

html {
  scroll-behavior: smooth;
}

#destiny_portal {
  display: block;
  float: right;
  position: fixed;
  z-index: 4;
  right: 170px;
  margin-top: 26px;
  cursor: pointer;
}

#epic_portal {
  display: block;
  float: right;
  position: fixed;
  z-index: 4;
  right: 310px;
  margin-top: 26px;
  cursor: pointer;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
  
}

/* Track */
::-webkit-scrollbar-track {
  /* background-color: #f1f1f1 0.5;
  opacity: 0.5; */
  /* background: #a11c21; */
  background: #d38610;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  /* background: #888;  */
  background: #a11c21;
  /* background: #d38610; */
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
  border-radius: 10px;
}

.roboto {
  font-family: 'Roboto', sans-serif;
}

@media screen and (max-width: 600px) {
  /* CSS for screens that are 600 pixels or less will be put in this section */
  #destiny_portal {
      display: none;
  }
  #epic_portal{
    display: none;
  }
}
