.popup_menu {
    height: 100%;
    width: 100%;
    display: none;
    position: fixed;
    z-index: 4;
    // background-color: rgba(0,0,0, 0.9);
    background: #a11c21;
    overflow-x: hidden;
    // animation-name: ease;
    transition: 0.5s;
}

.menu_link {
    position: relative;
    top: 30%;
    width: 100%;
    text-align: center;
}

.burger:active .link_text, .burger:active .link_site {
    transition: 8s;

    -webkit-animation: fadein 5s; /* Safari, Chrome and Opera > 12.1 */
       -moz-animation: fadein 5s; /* Firefox < 16 */
        -ms-animation: fadein 5s; /* Internet Explorer */
         -o-animation: fadein 5s; /* Opera < 12.1 */
            animation: fadein 5s;
}

.link_text {
    display: block;
    // padding: 8px;
    text-decoration: none;
    font-size: 50px;
    // color: #818181;
    color: #ffb105;
}

.link_text:hover, .link_text:focus, .popup_menu .close_btn:hover, .popup_menu .close_btn:focus {
    text-decoration: none;
    color: #f1f1f1;
}

.libraries {
    display: none;
}

.libraries h1 {
    // display: none;
    font-size: 50px;
    color: white;
}

.libraries a {
    display: block;
    font-size: 30px;
}

.libraries hr {
    height: 10px;
    border: 0;
    box-shadow: 0 10px 10px -10px #3a3a3a inset;
}

.popup_menu .close_btn {
    position: absolute;
    top: 20px;
    right: 3%;
    font-size: 60px;
    // color: #818181;
    color: #ffb105;
	cursor: pointer;
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Firefox < 16 */
@-moz-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Internet Explorer */
@-ms-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Opera < 12.1 */
@-o-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}


@media screen and (max-height: 450px) {
    .link_text {font-size: 20px}
    .popup_menu .close_btn {
        font-size: 40px;
        top: 15px;
        right: 35px;
    }
}

@media screen and (max-width: 600px) {
    /* CSS for screens that are 600 pixels or less will be put in this section */
    .menu_link {
        top: 10%
    }

    .libraries {
        display: block;
    }
}
