@media screen and (max-width: 849px) {
    /* CSS for screens that are 849 pixels or less will be put in this section */
    #dis_full_logo {
        display: none;
    }
    
    #dis_short_logo {
        display: block;
    }
}

@media screen and (max-width: 470px) {
    /* CSS for screens that are 470 pixels or less will be put in this section */
    #dis_short_logo {
        margin-top: 22px;
        margin-left: 0%;
        width: 90%;
        animation-name: fadeIn;
        animation-duration: 1s;
    }
}

@media screen and (max-width: 425px) {
    /* CSS for screens that are 425 pixels or less will be put in this section */
    #dis_short_logo {
        margin-top: 24px;
        margin-left: 0%;
        width: 70%;
        animation-name: fadeIn;
        animation-duration: 1s;
    }
}
